export default {
	data() {
		return {
			form:{
				additionalProp1:'0',
				additionalProp2:'0',
			},
			list1: [
				{
					value: '0',
					name: '健康',
					checked:true,
				},
				{
					value: '1',
					name: '有发烧咳嗽等症状'
				},
				{
					value: '2',
					name: '无发烧但有咳嗽胸闷等症状'
				},
				{
					value: '3',
					name: '无发烧但有其他症状'
				},
				{
					value:'4',
					name:'其他'
				}
			],
			list2:[
				{
					value:'0',
					name:'有确诊病历'
				},{
					value:'1',
					name:'有长途行程'
				},{
					value:'2',
					name:'在重点疫区'
				}
			]
		};
	},
	onLoad(e) {},
	methods: {
		handleRadioChange(evt,name){
			this.form[name] = evt.target.value
		},
		send(){
			let params = JSON.stringify({
				userid:'123',
				results:this.form
			})
			this.$HTTP({
				url:'/campusapp/userhealth/answer',
				root:'http://61.132.95.169:10105',
				params,
				successCallback:(res)=>{
					console.log(res,'res')
					uni.showToast({
						title:'提交成功!'
					})
				}
			})
		}
	}
};
